h1{
    text-align: center;
    margin-top: 3rem;
    font-family: 'Silkscreen', cursive;
    font-size: 45px;
}

.container{
    margin:auto;
    margin-top: 4rem;
    border: 1px solid black;
    width: min-content;
    padding-top: 1rem;
    padding-bottom: 1rem;
    border-radius: 17px;
    background-color: #e5e5e5;
}

.btn{
    border-radius: 4px;
    width: 4.9638rem;
    margin: 5px;
}
.blue-btn{
    background-color: #14213d;
    color: white;
}
.blue-btn:hover{
    background-color: #1f3660;
    color: white;
}
.yellow-btn{
    background-color: #fca311;
}
.yellow-btn:hover{
    background-color: #f6b752;
}
.delete{
    width: 10.6rem;
}
.white-btn{
    background-color: #fff;
}
.white-btn:hover{
    background-color: rgb(242, 235, 221);
}

#panel{
    background-color: white;
    border: 1px solid black;
    height: 57px;
    width: 350px;
    border-radius: 12px;
    margin: 5px;
    text-align: right;
    display: flex;
    justify-content: end;
    padding: 10px;
    font-size: 25px;
}
